import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import List from '@vkontakte/vkui/dist/components/List/List';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout';
import Tabs from '@vkontakte/vkui/dist/components/Tabs/Tabs';
import TabsItem from '@vkontakte/vkui/dist/components/TabsItem/TabsItem';
import HorizontalScroll from '@vkontakte/vkui/dist/components/HorizontalScroll/HorizontalScroll';
import Link from '@vkontakte/vkui/dist/components/Link/Link';
import styles from "./ListUsers.module.sass";
import PanelHeaderBack from "@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack";

const ListItem = ({idItem,first_name,last_name,avatar,price,pay,name,test}) => {

    return (
        <Div className={styles.div}>
            <span className={styles.listNumber}>{idItem}</span>
            <Cell
                className={styles.listName}
                before={avatar ? <Avatar src={avatar}/> : <Avatar src='https://vk.com/images/camera_200.png'/>}
            >
                {first_name ? first_name + " " + last_name : "Место еще не заняли"}
            </Cell>
            <Button id={idItem} className={styles.button}>
                {price} руб
            </Button>
            <Button id={idItem} className={styles.button} level="commerce" onClick={pay}>
                Занять место
            </Button>
        </Div>


    );
};

export default ListItem;



